import React from 'react'
import styled from "styled-components"

class Textarea extends React.Component {
  constructor (props) {
    super(props)
    // console.log(props)
    this.state = {
      selected_item: this.props.selected_item || false
    }
    this._className = this._className.bind(this)
  }

  handleFocus = (event) => {
    event.target.select();
  }

  _className () {
    if (this.props.className === undefined) {
      this.setState.className = ''
    } else {
      this.setState.className = this.props.className
    }
  }

  renderLabel = () =>
    { if (this.props.label) {
        return <label htmlFor={this.props.id}>{this.props.label}</label>
      } else {
        return ''
      }
    }

  render () {

    return (
      <div className={`input ${this.props.className}`}>
        {this.renderLabel()}
        <textarea onFocus={(event) => this.handleFocus(event)} className={`${this.props.className}`} name={this.props.id} id={this.props.id} defaultValue={ !this.props.placeholder ? 'Type...' : this.props.placeholder }></textarea>
      </div>
    )
  }
}

class Checkbox extends React.Component {
  constructor (props) {
    super(props)
    // console.log(props)
    this.state = {
      checked: false
    }
  }

  renderLabel = () =>
    { if (this.props.label) {
        return <label htmlFor={this.props.id}>{this.props.label}</label>
      } else {
        return ''
      }
    }

  render () {

    return (
      <div className={`custom-checkbox`}>
        <input id={`${this.props.id}`}
          type="checkbox"
          defaultChecked={this.props.defaultChecked}
          onChange={this.props.onChange}
          className={this.props.className}
        />
        <label htmlFor={`${this.props.id}`} className="check-box"></label>
        {this.renderLabel()}
        <span className="desc">{this.props.children}</span>
      </div>
    )
  }
}

const InnerStyle = (props) => {
  return `
    .custom-checkbox {
      position: relative;
      padding: 1.2rem 1rem 0 0;
      display: grid;
      grid-template-columns: 1.5rem auto;
      grid-template-areas: "checkbox label"
                          "checkbox description";
      grid-gap: 0.5rem;
      .desc {
        grid-area: description;
        font-size: 1.4rem;
        p { font-size: 1.4rem; }
      }
      input {
        margin: 0;
        display: none;
      }
      input[type=checkbox]:checked + .check-box,
      .check-box.checked {
        border-color: ${props.green};
      }
      input[type=checkbox]:checked + .check-box::after,
      .check-box.checked::after {
        height: 0.5rem;
        -moz-animation: dothabottomcheck 0.2s ease 0s forwards;
        -o-animation: dothabottomcheck 0.2s ease 0s forwards;
        -webkit-animation: dothabottomcheck 0.2s ease 0s forwards;
        animation: dothabottomcheck 0.2s ease 0s forwards;
      }
      input[type=checkbox]:checked + .check-box::before,
      .check-box.checked::before {
        height: 2rem;
        -moz-animation: dothatopcheck 0.4s ease 0s forwards;
        -o-animation: dothatopcheck 0.4s ease 0s forwards;
        -webkit-animation: dothatopcheck 0.4s ease 0s forwards;
        animation: dothatopcheck 0.4s ease 0s forwards;
      }
      label {
        margin: 0;
        padding: 0;
      }
      .check-box {
        width: 1.5rem;
        flex: 0 0 auto;
        overflow: visible;
      }
    }
    // Custom Checkbox
    @keyframes dothabottomcheck {
      0% {
        height: 0;
      }
      100% {
        height: 0.5rem;
      }
    }
    @keyframes dothatopcheck {
      0% {
        height: 0;
      }
      50% {
        height: 0;
      }
      100% {
        height: 1.5rem;
      }
    }

    .check-box {
      height: 1.5rem;
      width: 1.5rem;
      background-color: ${props.bg};
      border: 1px solid ${props.linkColor};
      position: relative;
      display: inline-block;
      -moz-box-sizing: border-box;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      -moz-transition: border-color ease 0.2s;
      -o-transition: border-color ease 0.2s;
      -webkit-transition: border-color ease 0.2s;
      transition: border-color ease 0.2s;
      cursor: pointer;
    }

    .check-box::before, .check-box::after {
      -moz-box-sizing: border-box;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      position: absolute;
      height: 0;
      width: 0.3rem;
      background-color: ${props.green};
      display: inline-block;
      -moz-transform-origin: left top;
      -ms-transform-origin: left top;
      -o-transform-origin: left top;
      -webkit-transform-origin: left top;
      transform-origin: left top;
      content: ' ';
      -webkit-transition: opacity ease .5;
      -moz-transition: opacity ease .5;
      transition: opacity ease .5;
    }

    .check-box::before {
      top: 11px;
      left: 7px;
      -moz-transform: rotate(-135deg);
      -ms-transform: rotate(-135deg);
      -o-transform: rotate(-135deg);
      -webkit-transform: rotate(-135deg);
      transform: rotate(-135deg);
    }

    .check-box::after {
      top: 6px;
      left: 2px;
      -moz-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
      -o-transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
      transform: rotate(-45deg);
    }

    .verticalForm {
      display: grid;
    }

    .horizontalForm {
      display: grid;
      grid-template-columns: 1fr 3fr;
      gap: 1rem;
    }

    .groupForm {
      display: grid;
      position: relative;
      label {
        width: 100%;
        padding: 0 1rem;
        position: absolute;
        top: 0.5rem;
      }
      label, input, select, textarea {
        width: 100%;
      }
      input[type="text"],input[type="password"],input[type="search"],input[type="number"],input[type="email"], select, textarea {
        outline: none;
        -webkit-appearance: none;
        padding: 2.5rem 1rem 1.5rem;
        line-height: 1.3;
        border-radius: 0;
      }
    }`
}

const FormStyle = (props) => {
  return InnerStyle(props)
}

const InputS = styled.div`
  ${ props => InnerStyle(props.theme) }
`

class Input extends React.Component {
  constructor (props) {
    super(props)
    // console.log(props)
    this.state = {
      selected_item: this.props.selected_item || false
    }
  }

  renderLabel = () =>
    { if (this.props.label) {
        return <label htmlFor={this.props.id}>{this.props.label}</label>
      } else {
        return ''
      }
    }

  renderType = () =>
    { switch (this.props.type) {
      case 'textarea':
        return <Textarea {...this.props} />
      case 'checkbox':
        return <Checkbox {...this.props} />
        // break;
      default:
        return <div className={`input ${this.props.className}`}>
          {this.renderLabel()}
          <input
            id={this.props.id}
            type={this.props.type}
            value={this.props.value}
            placeholder={ !this.props.placeholder ? 'Type...' : this.props.placeholder }
          />
        </div>
    }
  }

  render () {
    return (
      <InputS>{this.renderType()}</InputS>
    )
  }
}


export {Input, FormStyle};
