import React from 'react'
import ReactDOM from 'react-dom'
import { I18n } from 'react-i18nify-lite'
import { HashRouter, Route, Switch } from 'react-router-dom'
import posed, { PoseGroup } from 'react-pose'
import {ThemeProvider, createGlobalStyle} from 'styled-components'

import {Core, LayoutStyle, IconStyle, ButtonStyle, FeedbackStyle, DialogStyle} from 'prey-stash'
import {useDark, useLight} from './config'

import androidLogin from "./android/login"
import androidSignUp from "./android/signup"
import androidActivation from "./android/activation"
import androidReport from "./android/report"

import Android from "./Android"
import androidPermissions from "./android/permissions"
import androidSecurity from "./android/security"
import androidOnBoarding from "./android/start"
import androidWhatsPrey from "./android/whatsprey"

import androidEmailSent from "./android/emailsent"
import androidEmailRetry from "./android/emailretry"
import androidEmailOk from "./android/emailok"

import iosLogin from "./ios/login"
import iosActivation from "./ios/activation"
import iosReport from "./ios/report"

import iosHome from "./ios/home"
import iosSignUp from "./ios/signup"
import iosPermissions from "./ios/permissions"
import iosOnBoarding from "./ios/start"
import iosWhatsPrey from "./ios/whatsprey"
import iosEmailSent from "./ios/emailsent"

// import iosCamo from "./ios/hidden"

import OnBoardingStyles from './onboarding/onboardingstyles'

import {initLang} from './i18n/android'

import './assets/typography/style.css'

const onboardingType = process.env.REACT_APP_ONBOARDING_TYPE || 'android'

const DarkMode = createGlobalStyle`
  ${Core(useDark)}
  ${LayoutStyle(useDark)}
  ${ButtonStyle(useDark)}
  ${DialogStyle(useDark)}
  ${IconStyle(useDark)}
  ${FeedbackStyle(useDark)}
  ${OnBoardingStyles(useDark)}
`
const LightMode = createGlobalStyle`
  ${Core(useLight)}
  ${LayoutStyle(useLight)}
  ${ButtonStyle(useLight)}
  ${DialogStyle(useLight)}
  ${IconStyle(useLight)}
  ${FeedbackStyle(useLight)}
  ${OnBoardingStyles(useLight)}
`

const RouteContainer = posed.div({
  init: {
    opacity: 0
  },
  enter: {
    opacity: 1,
    beforeChildren: true,
    staggerChildren: 50
  },
  exit: {
    opacity: 0,
    staggerChildren: 20,
    staggerDirection: -1
  }
});

const paths = [
  {url: "/", component: Android},
  {url: "/index", component: Android},
  {url: "/activation", component: androidActivation},
  {url: "/signin", component: androidLogin},
  {url: "/signup", component: androidSignUp},
  {url: "/report", component: androidReport},

  {url: "/permissions", component: androidPermissions},
  {url: "/security", component: androidSecurity},
  {url: "/start", component: androidOnBoarding},
  {url: "/whatsprey", component: androidWhatsPrey},

  {url: "/emailsent", component: androidEmailSent},
  {url: "/emailretry", component: androidEmailRetry},
  {url: "/emailok", component: androidEmailOk}

]

const iospaths = [
  {url: "/", component: iosHome},
  {url: "/index", component: iosHome},
  {url: "/activation", component: iosActivation},
  {url: "/signin", component: iosLogin},
  {url: "/signup", component: iosSignUp},
  {url: "/report", component: iosReport},

  {url: "/permissions", component: iosPermissions},
  {url: "/start", component: iosOnBoarding},
  {url: "/whatsprey", component: iosWhatsPrey},
  // {url: "/hidden", component: iosCamo}
  {url: "/emailsent", component: iosEmailSent}
]

class App extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      currentTheme: 'dark',
      palette: []
    }
  }
  componentWillMount () {
    initLang()
    var theme = this.getAllUrlParams(window.location.href).theme;
    window.theme = theme;
    this.setState({
      currentTheme: theme,
      palette: theme === 'dark' ? useDark : useLight
    })
  }
  getAllUrlParams (url) {
    var queryString = url ? url.split('?')[1] : window.location.search.slice(1);
    var obj = {};
    if (queryString) {
      queryString = queryString.split('#')[0];
      var arr = queryString.split('&');

      for (var i = 0; i < arr.length; i++) {
        var a = arr[i].split('=');
        var paramName = a[0];
        var paramValue = typeof (a[1]) === 'undefined' ? true : a[1];
        paramName = paramName.toLowerCase();
        if (typeof paramValue === 'string') paramValue = paramValue.toLowerCase();
        if (paramName.match(/\[(\d+)?\]$/)) {
          var key = paramName.replace(/\[(\d+)?\]/, '');
          if (!obj[key]) obj[key] = [];
          if (paramName.match(/\[\d+\]$/)) {
            var index = /\[(\d+)\]/.exec(paramName)[1];
            obj[key][index] = paramValue;
          } else {
            obj[key].push(paramValue);
          }
        } else {
          if (!obj[paramName]) {
            obj[paramName] = paramValue;
          } else if (obj[paramName] && typeof obj[paramName] === 'string'){
            obj[paramName] = [obj[paramName]];
            obj[paramName].push(paramValue);
          } else {
            obj[paramName].push(paramValue);
          }
        }
      }
    }

    return obj;
  }
  renderer = () => {
    if (onboardingType === "android"){
      return (
        <Route
          render={
            ({ location, match }) => (
              <PoseGroup>
                <RouteContainer key={location.pathname}>
                  <Switch location={location}>
                    {
                      paths.map((item, key)=>{
                        var setLang = location.pathname.match(/\/(es|en)\//)
                        var path = item.url
                        var lang
                        if(setLang && setLang.length > 0 ){
                          lang = `/${setLang[1]}`
                          I18n.setLocale(setLang[1])
                        } else {
                          lang = ''
                          I18n.setLocale('en')
                        }
                        path = `${lang}${item.url}`

                        return <Route
                          exact
                          path={path}
                          render={(props) => <item.component {...props} palette={this.state.palette} />}
                          key={key}
                        />
                      })
                    }
                  </Switch>
                </RouteContainer>
              </PoseGroup>
          )}
        />
      )
    } else {
      return (
        <Route
          render={
            ({ location, match }) => (
              <PoseGroup>
                <RouteContainer key={location.pathname}>
                  <Switch location={location}>
                    {
                      iospaths.map((item, key)=>{
                        var setLang = location.pathname.match(/\/(es|en)\//)
                        var path = item.url
                        var lang
                        if(setLang && setLang.length > 0 ){
                          lang = `/${setLang[1]}`
                          I18n.setLocale(setLang[1])
                        } else {
                          lang = ''
                          I18n.setLocale('en')
                        }
                        path = `${lang}${item.url}`
                        return <Route
                          exact
                          path={path}
                          render={(props) => <item.component {...props} palette={this.state.palette} />}
                          key={key}
                        />
                      })
                    }
                  </Switch>
                </RouteContainer>
              </PoseGroup>
          )}
        />
      )
    }
  }
  render() {
    return(
      <ThemeProvider theme={{mode: this.state.currentTheme}}>
        <>
          {
            this.state.currentTheme === 'light' ? <LightMode /> : <DarkMode />
          }
          {this.renderer()}
        </>
      </ThemeProvider>
    )
  }
}

ReactDOM.render(
  <HashRouter>
    <App />
  </HashRouter>,
  document.getElementById('root')
);
