import React from 'react'
import { Link } from 'react-router-dom'
import { Group, Space } from 'prey-stash'
import logomono from '../images/branding/prey-logo-monotono-alt.svg'
import { I18n } from 'react-i18nify-lite'

import { Input } from '../assets/Input/Input'

export default class Login extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {}
    }
  }

  errorsFor = (name)=>{
      const errors = this.state.data[name]
      if(!errors) return
      if(errors.length === 0) return

      return <span className="msg info-error">{
        errors.map((o)=>(
            <span>{o}</span>
          )
        )}
        </span>
  }

  handleClickLogin = (e)=>{
    var emaillogin=encodeURIComponent(document.getElementById('emaillogin').value)
    var passlogin=encodeURIComponent(document.getElementById('passlogin').value)
    var url = 'ioslogin://parameters?preyEmailLogin='+emaillogin+'&preyPassLogin='+passlogin;
    this.openCustomURLinIFrame(url);
  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  handleClickQR = (e)=>{
      var url = 'iosqrcode://';
      this.openCustomURLinIFrame(url);
  }

  handleShowTerms = (e)=>{
      var url = 'iosterms://';
      this.openCustomURLinIFrame(url);
  }

  handleShowPrivacy = (e)=>{
      var url = 'iosprivacy://';
      this.openCustomURLinIFrame(url);
  }

  handleShowForgot = (e)=>{
      var url = 'iosforgot://';
      this.openCustomURLinIFrame(url);
  }

  openCustomURLinIFrame = (src)=> {
      var rootElm = document.documentElement;
      var newFrameElm = document.createElement("IFRAME");
      newFrameElm.setAttribute("src",src);
      rootElm.appendChild(newFrameElm);
      //remove the frame now
      newFrameElm.parentNode.removeChild(newFrameElm);
  }


  render() {
    return  <div className="login fs-onboarding regular inverted">
    <div className="column">
      <div className="sign-form">
        <form autoComplete="off" className="form form-session" id="new-session" acceptCharset="UTF-8" onFocus={this.handleFocus}>
          <h1>{I18n.t("signin.title")}</h1>
          <p className="lead">{I18n.t("signin.subtitle")}</p>
          <Input
            label={I18n.t('signin.field1')}
            className="verticalForm"
            id="emaillogin"
            placeholder=" "
            type="email"
            onFocus={this.handleFocus}
            />
          <Input
            label={I18n.t('signin.field2')}
            className="verticalForm"
            id="passlogin"
            placeholder=" "
            type="password"
            onFocus={this.handleFocus}
            />
          <br/>
          {this.errorsFor("error")}
          <Group>
            <Link tabIndex="3" to="#" onClick={this.handleClickLogin} className="btn cta success xl">{I18n.t('signin.button_submit')}</Link>
            <Group>
              <Space />
              <Link tabIndex="4" to="#" onClick={this.handleClickQR} className="btn alt cta">&nbsp; {I18n.t('signin.button_qr')} &nbsp;</Link>
            </Group>
          </Group>
        </form>

          <p className="here">{I18n.t('signin.maybe')}</p>
          <Link className="btn-link btn-block" to={`/${I18n._locale}/signup`}>{I18n.t('signin.button_account')}</Link>
          <a className="btn-link btn-block" onClick={this.handleShowForgot}>{I18n.t('button_forgot')}</a>
      </div>
      <div id="footer">
        <footer className="copy">
          <div className="footer-links">
            <a onClick={this.handleShowTerms} rel="noopener noreferrer">{I18n.t('button_terms')}</a>
            <a onClick={this.handleShowPrivacy} rel="noopener noreferrer">{I18n.t('button_privacy')}</a>
          </div>
          <svg viewBox="0 0 512 200" xmlSpace="preserve" >
            <path
              className="fontColor"
              d="M477 71.4v57.2h-65.4v-8.2h57.2v-8.1h-57.2V71.4h8.2v32.7h49V71.4zM191 71.4v57.2h8.2v-16.3h57.2V71.4H191zm57.2 32.7h-49V79.6h49v24.5zM329.9 71.4v8.2h-57.2v32.7h-8.1V71.4zM338.1 71.4v40.9h65.4v-8.2h-57.2v-8.2h57.2V71.4h-65.4zm57.2 16.3h-49v-8.2h49v8.2zM98 40.7L78.1 52.2l13.2 7.6L98 56l6.6 3.8 13.2-7.6zM98 159.3l49.7-28.7V100l-13.3 7.7V123L98 144l-36.5-21v-15.4L48.3 100v30.6z"
            />
            <path
              className="fontColor"
              d="M154.3 65.6v15.3l-46.4 26.8 13.3 7.6L98 128.7l-23.2-13.4 13.2-7.6-46.4-26.8V65.6L35 61.7V42.6l56.3 32.5 6.7-3.8 6.6 3.8 56.3-32.5v19.1z"
            />
          </svg>
        </footer>
      </div>
    </div>
  </div>
 }

}
