import React from 'react'
import { Link } from 'react-router-dom'
import { Group, Space } from 'prey-stash'
import logomono from '../images/branding/prey-logo-monotono-alt.svg'
import { I18n } from 'react-i18nify-lite'
import { Loading } from '../assets/Dialog'
import { Input } from '../assets/Input/Input'

const handleClickQR = (e)=>{
  if(window.Android){
    window.Android.qr();
  } else {
    alert("ola oso qr" )
  }
}

export default class Login extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {},
      isLoading: false
    }
  }

  errorsFor = (name)=>{
      const errors = this.state.data[name]
      if(!errors) return
      if(errors.length === 0) return

      return <span className="msg info-error">{
        errors.map((o)=>(
            <span>{o}</span>
          )
        )}
        </span>
  }

  handleClickLogin = (e)=>{
    this.setState({
        data: {} ,
        isLoading: true
      }, ()=>{setTimeout(this.callHandleClickLogin, 0)
    })
  }
  callHandleClickLogin = (e)=>{
    var emaillogin=document.getElementById('emaillogin').value
    var passlogin=document.getElementById('passlogin').value
    if(window.Android){
        let json=window.Android.mylogin(''+emaillogin,''+passlogin);
        if(json!==''){
          this.setState({
              data: JSON.parse(json),
              isLoading: false
            })
        }else{
          this.props.history.push(`/${I18n._locale}/activation`);
        }
    } else {
      let json='{"error":["Credenciales inválidas"]}';
      this.setState({
        data: JSON.parse(json),
        isLoading: false
      })
      //this.props.history.push(`/${I18n._locale}/activation`);
      //alert("ola oso emaillogin:"+emaillogin+"  passlogin:"+passlogin )
    }
  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }


  render() {
    return  <div className="login fs-onboarding regular inverted">
    <div className="column">
      <div className="sign-form">
        <form autoComplete="off" className="form form-session" id="new-session" acceptCharset="UTF-8" onFocus={this.handleFocus}>
          <h1>{I18n.t("signin.title")}</h1>
          <p className="lead">{I18n.t("signin.subtitle")}</p>
          <Input
            label={I18n.t('signin.field1')}
            className="verticalForm"
            id="emaillogin"
            placeholder=" "
            type="email"
            onFocus={this.handleFocus}
            />
          <Input
            label={I18n.t('signin.field2')}
            className="verticalForm"
            id="passlogin"
            placeholder=" "
            type="password"
            onFocus={this.handleFocus}
            />
          <br/>
          {this.errorsFor("error")}
          <Group>
            <Link tabIndex="3" to="#" onClick={this.handleClickLogin} className="btn cta success xl">{I18n.t('signin.button_submit')}</Link>
            <Group>
              <Space />
              <Link tabIndex="4" to="#" onClick={handleClickQR} className="btn alt cta">&nbsp; {I18n.t('signin.button_qr')} &nbsp;</Link>
            </Group>
          </Group>
        </form>

          <p className="here">{I18n.t('signin.maybe')}</p>
          <Link className="btn-link btn-block" to={`/${I18n._locale}/signup`}>{I18n.t('signin.button_account')}</Link>
          <a className="btn-link btn-block" href="https://panel.preyproject.com/forgot">{I18n.t('button_forgot')}</a>

          {
            this.state.isLoading ? (
              <Loading><code>{I18n.t(`dialog_login.loading`)}</code></Loading>
            ) : null
          }

      </div>


      <div id="footer">
        <footer className="copy">
          <div className="footer-links">
            <a href="https://preyproject.com/terms" target="_blank" rel="noopener noreferrer">{I18n.t('button_terms')}</a>
            <a href="https://preyproject.com/privacy" target="_blank" rel="noopener noreferrer">{I18n.t('button_privacy')}</a>
            <a href="https://www.preyproject.com/privacy#cookies" target="_blank" rel="noopener noreferrer">{I18n.t('button_cookies')}</a>
          </div>
          <svg viewBox="0 0 512 200" xmlSpace="preserve" >
            <path
              className="fontColor"
              d="M477 71.4v57.2h-65.4v-8.2h57.2v-8.1h-57.2V71.4h8.2v32.7h49V71.4zM191 71.4v57.2h8.2v-16.3h57.2V71.4H191zm57.2 32.7h-49V79.6h49v24.5zM329.9 71.4v8.2h-57.2v32.7h-8.1V71.4zM338.1 71.4v40.9h65.4v-8.2h-57.2v-8.2h57.2V71.4h-65.4zm57.2 16.3h-49v-8.2h49v8.2zM98 40.7L78.1 52.2l13.2 7.6L98 56l6.6 3.8 13.2-7.6zM98 159.3l49.7-28.7V100l-13.3 7.7V123L98 144l-36.5-21v-15.4L48.3 100v30.6z"
            />
            <path
              className="fontColor"
              d="M154.3 65.6v15.3l-46.4 26.8 13.3 7.6L98 128.7l-23.2-13.4 13.2-7.6-46.4-26.8V65.6L35 61.7V42.6l56.3 32.5 6.7-3.8 6.6 3.8 56.3-32.5v19.1z"
            />
          </svg>
        </footer>
      </div>
    </div>
  </div>
 }

}
