import React from "react"
import ReactDOM from "react-dom"
import styled from "styled-components"
import posed from "react-pose"


export class Portal extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount () {
    this.element = document.querySelector(`#${this.props.selector}`)
    this.forceUpdate()
  }

  render () {
    if (!this.element) {
      return null
    }
    return ReactDOM.createPortal(this.props.children, this.element)
  }
}

const StyledFrame = styled.div`
  position: fixed;
  top:0;right:0;bottom:0;left:0;
  background-color: rgba(0,0,0,0.60);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50000;
`

const Frame = posed(StyledFrame)({
  init: {x: 50, opacity: 0},
  enter: {
    x: 0,
    opacity: 1,
    beforeChildren: true,
    staggerChildren: 30
  },
  exit: {
    x: 50,
    opacity: 0,
    staggerChildren: 30
  }
})

class Loading extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      isLoading: false
    }
  }
  render() {
    return(
      <Frame key={'loading'} {...this.props}>
        {this.props.children}
      </Frame>
    )
  }
}

class Dialog extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      showDialog: false
    }
  }
  render() {
    return (
      <Frame key={'dialog'} {...this.props}>
        {this.props.children}
      </Frame>
    )
  }
}

export {Dialog, Loading}
